/* this is a css file for modifing details of the event cards that show in the upcoming column in home page */
 
/* the date and time in the event card */
.datetime{
    font-size: 18px;
    text-align: right;
}

/* the event title that shows in the event card */
.title{
    font-size: 25px;
    font-weight: 300;
    font-style: oblique;
    word-break: break-word;
    text-overflow: ellipsis;
    overflow: hidden;
    -webkit-line-clamp: 2;
    max-width: 100%;
    -webkit-box-orient: vertical;
    display: block;
    display: -webkit-box;
    /* background-color: rgb(217, 83, 79); */
    
    border-top:solid;
    border-bottom: solid;
    border-color: Gainsboro;
   
    /* color:#fef0db;  */
    
    padding-left: 5px;
}

/* The details of the container for the event card */
.card-container{
    position: relative;
    border-radius: 11px;
    width: 95%;
    max-width: 330px;
    overflow : hidden;
    box-shadow : 0px 0px 15px -5px;
    background-color: white;
    margin: 20px auto;
    color: #282828;
    border: groove;
    /* height: 470px; */
    display: flex;
    flex-direction: column;
}

/* The container that showing  the image in the card*/
.image-container{
    overflow: hidden;
    text-align: center;
}

/* The size and showing option of the image in the card */
.image-container img{
    overflow: hidden;
    height: auto;
    width: 100%;
}

/* The container for showing different informations of the event in the card */
.info-container{
    overflow: hidden;
    border-top-color: #d9534f;
    display: grid;
    padding: 5px 10px;
}

/* modify the style of the part that shows the event organizer in the card */
.organizer{
    font-size: 15px;
    font-weight: 350;
    text-align: left;
    display: block;
    display:-webkit-box;
    -webkit-line-clamp: 1;
    line-height: 100%;
    padding: 3px 3px 5px 5px;
}

/* modify the style of the part that shows the date and time of the event in the card*/
.datetime{
    font-size: 15px;
    font-weight: 350;
    text-align: left;
    padding: 3px 3px 5px 5px;
    
}

/* modify the style of the part that shows the venue in the card */
.venue{
    font-size: 15px;
    font-weight: 350;
    text-align: left;
    display: block;
    display:-webkit-box;
    -webkit-line-clamp: 1;
    line-height: 100%;
    padding: 3px 3px 5px 5px;
}

/* modify the style of the part that shows the word "Description:" in the card*/
.description-head{
    font-size: 20px;
    font-weight: 400;
    font-style: oblique;
    text-align: left;
    display: block;
    display:-webkit-box;
    -webkit-line-clamp: 1;
    margin: 5px 0;
    line-height: 1em;
    padding-left: 5px;
}

/* modify the style of the part that shows  */
.description-body{
    font-size: 15px;
    font-weight: 300;
    text-align: left;
    /* display: block; */
    display:-webkit-box;
    overflow: hidden;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    line-height: normal;
    word-break: break-word;
    padding-left: 5px;
    padding-right: 5px;
    width:93%;
    margin-bottom: 0;
    /* padding-top: 0; */
    /* margin-bottom: 0; */
}

/* the container for the button inside the card */
.card-btn{
    margin-top: auto;
    margin-left: auto;
    margin-right: auto;
    /* margin: auto; */
    width: 90%;
    padding: 10px 0;
}

/* modify the style of the button inside the card */
.card-btn button{
    background-color: rgba(217, 83, 79, 0.9);
    border: none;
    border-radius: 6px;
    font-size: 25px;
    font-weight: 300;
    color:#fef0db;
    cursor: pointer;
    transition: 0.3s;
    animation: ease-in-out;
    width: 100%;
    /* margin-top: 1px ; */
}

/* create effect for the button when user hover on the button */
 .card-btn button:hover{
    transform: scale(1.05);
    background-color: rgba(217, 83, 79, 1);
    
}

